<template>
  <v-card :key="keyform" outlined :width="getSize()" class="transparent">
    <iframe name="dummyframe" id="dummyframe" style="display: none;"></iframe>
    <v-card-title v-if="!sameses" class="justify-center text-center white--text meticula">
      <div v-if="!submitted">Fill Your Information &</div>

      <div v-if="!submitted">We Will Contact You</div>
    </v-card-title>
    <form
      action="https://docs.google.com/forms/u/0/d/e/1FAIpQLSedl12CJx-DKsjyoN4IsHL6Z-hEMYXe2oRAkOd4wGo2nbQP9g/formResponse"
      method="POST"
      v-if="!sameses"
      target="dummyframe"
      id="theForm"
      name="theForm"
    >
      <v-text-field
        v-model="name"
        :error-messages="nameErrors"
        :counter="10"
        label="Name"
        jsname="YPqjbf"
        name="entry.2005620554"
        class="contentborder"
        :hidden="submitted"
        required
        @input="$v.name.$touch()"
        @blur="$v.name.$touch()"
      ></v-text-field>
      <v-text-field
        v-model="phone"
        :error-messages="phoneErrors"
        :counter="10"
        label="Phone Number"
        name="entry.1166974658"
        class="contentborder"
        :hidden="submitted"
        required
        @input="$v.phone.$touch()"
        @blur="$v.phone.$touch()"
      ></v-text-field>
      <v-text-field
        v-model="email"
        :error-messages="emailErrors"
        label="E-mail"
        name="entry.1045781291"
        class="contentborder"
        :hidden="submitted"
        required
        @input="$v.email.$touch()"
        @blur="$v.email.$touch()"
      ></v-text-field>

      <div>
        <div v-if="submitted">
          <v-card-title class="justify-center text-center white--text meticula">
            <br />
            <div>Thank You For Your Submission</div>
          </v-card-title>
        </div>
        <br />
        <br />
        <v-text-field hidden v-model="extra" name="entry.839337160"></v-text-field>

        <v-btn @click="sub" :hidden="submitted" class="white cyan--text center1">submit</v-btn>
      </div>
    </form>
    <div v-if="sameses">
      <br />
      <br />
      <br />
      <br />
      <v-card-title class="justify-center text-center white--text meticula">
        <div>Sorry, Only One Submission Per</div>
        <div>Session Allowed</div>
      </v-card-title>
    </div>
  </v-card>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  email,
  minLength,
  numeric
} from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    phone: {
      required,
      numeric,
      maxLength: maxLength(10),
      minLength: minLength(7)
    },
    email: { required, email },
    select: { required },
    checkbox: {
      checked(val) {
        return val;
      }
    }
  },

  data: () => ({
    name: "",
    email: "",
    phone: "",
    extra: "",
    select: null,
    action: "",
    items: ["One-Page Website", "Resume Website", "Full Website", "Other"],
    checkbox: false,
    submitted: false,
    sameses: localStorage["submitted"],
    submited: { name: "", email: "", select: "" },
    keyform: 0,
    windowWidth: window.innerWidth,
    trueval: true
  }),

  computed: {
    checkboxErrors() {
      const errors = [];
      if (!this.$v.checkbox.$dirty) return errors;
      !this.$v.checkbox.checked && errors.push("You must agree to continue!");
      return errors;
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("Name must be at most 10 characters long");
      !this.$v.name.required && errors.push("Name is required.");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Must be valid e-mail");
      !this.$v.email.required && errors.push("E-mail is required");
      return errors;
    },
    phoneErrors() {
      const errors = [];
      if (!this.$v.phone.$dirty) return errors;
      !this.$v.phone.numeric && errors.push("Must not contain Letters");
      !this.$v.phone.minLength &&
        errors.push("Phone Must be at least 7 numbers long");
      !this.$v.phone.maxLength &&
        errors.push("Phone must be at most 10 characters long");
      !this.$v.phone.required && errors.push("Phone is required.");
      return errors;
    }
  },

  methods: {
    sub() {
      document.getElementById("theForm").submit();
      this.submitted = true;
      localStorage["submitted"] = true;
      // this.keyform += 1;
      // this.$v.$touch();
      // console.log(document.getElementById("theForm"));
    },
    getForm() {},
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.checkbox = false;
    },
    getSize() {
      if (this.windowWidth < 1200) {
        return "";
      } else {
        return "800";
      }
    }
  }
};
</script>
<style scoped>
@font-face {
  font-family: "meticula";
  src: url(../assets/Meticula-Regular.ttf);
  font-style: normal;
  font-weight: 100;
}
.meticula {
  font-family: meticula !important;
  font-size: calc(15px + 0.5vw);
}
.center1 {
  width: 40%;
  left: 30%;
}

.contentborder {
  border-style: none none none none;
  border-width: 1px;
}
</style>
